@media only screen and (max-width: 600px){
    body{
        padding: 0;
        margin: 0;
    }
    .tech-container{
        width: 100%;
        height: 100vh;
        background-image: url('../imgs/Bitmap-tech-mob.jpg');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .tech-section-one{
        padding-top: 88px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-content: center;
        gap: 32px;
    }
    .tech-heading {
    width: 176px;
    text-align: center;
    right: 0;
    left: 0;
    margin: auto;
    }
    .tech-heading span{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    color: #FFFFFF;
    mix-blend-mode: normal;
    opacity: 0.25;
    }
    .tech-heading p{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    text-transform: uppercase;
    color: #FFFFFF;
    display: inline;
    }

    .tech-img{
        width: 100%;
        padding-right: 0;
        padding-left: 0;
    }
    .tech-img img{
        max-height:100%;
        max-width:100%;
        object-fit: contain;
    }
    .tech-section-two {
        margin-top: 34px;
        display: flex;
        flex-direction: column;
        gap: 26px;
    }
    .tech-controller{
        display: flex;
        flex-direction: row;
        align-content: center;
        align-items: center;
        justify-content: center;
        gap: 16px;
    }
    .tech-btn{
        width: 40px;
        height: 40px;
        border-radius: 100%;
        mix-blend-mode: normal;
        /* opacity: 0.25; */
        border: 1px solid #ffffff3a;
        background: transparent;
        color: white ;
        transition: all .3s ease-in-out;
        font-family: 'Bellefair';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 18px;
        text-align: center;
        letter-spacing: 1px;
    }
    #tech-active {
        border: 1px solid #FFFFFF !important;
        background: #FFFFFF;
        color: #0B0D17;
    }
    .tech-info {
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: center;
        align-items: center;
        gap: 16px;
        width: 327px;
        right: 0;
        left: 0;
        margin: auto;
    }
    .tech-title{
        font-family: 'Barlow Condensed';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        letter-spacing: 2.3625px;
        color: #D0D6F9;
    }
    .tech-name{
        font-family: 'Bellefair';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
    }
    .tech-describe{
        font-family: 'Barlow';
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 25px;
        text-align: center;
        color: #D0D6F9;
    }
}
