
@media only screen and (max-width: 600px) {
    .dest-container{
        background-image: url('../imgs/Bitmap-destination-mob.jpg');
        width: 100%;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
    }
    .section-one{
        display: flex;
        justify-content: space-between;
        gap: 30px;
        flex-direction: column;
        align-items: center;
        padding-top: 88px;
    }
    .heading {
        width: 221px;
        text-align: center;
        right: 0;
        left: 0;
        margin: auto;
    }
    .heading span{
        font-family: 'Barlow Condensed';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 2.7px;
        color: #FFFFFF;
        opacity: 0.25;
        /* margin-top: 88px; */
}
    .heading p{
        font-family: 'Barlow Condensed';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 2.7px;
        text-transform: uppercase;
        color: #FFFFFF;
        display: inline;
}
.planet-img{
    width: 170px;
    height: 170px;
    right: 0;
    left: 0;
    margin: auto;
    /* margin-top: 51px; */
}
.planet-img img {
    width: 170px;
    height: 170px;
    right: 0;
    left: 0;
    margin: auto;
}
    /* Planet photo animation */
.rotate {
    animation: rotation 25s infinite linear;
}
@keyframes rotation {
    from {
    transform: rotate(0deg);
    }
    to {
    transform: rotate(359deg);
    }
}
.data-controller{
    left: 0;
    right: 0;
    margin: auto;
    /* margin-top: 25px; */
    width: 237.5px;
    display: flex;
    justify-content: space-around;
}
.data-controller button{
    padding: 0;
    background: none;
    border: none;
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    height: 30px;
    line-height: 30px;
    color: #D0D6F9;
    text-transform: uppercase;
}
.data-controller button:after{
    width: 0;
    display:inline;
    background:#FFFFFF;
    height:3px;
    transition: all .5s ease-in-out;
    content: "" ;
    float: left;
}
.data-controller button:hover{
    color:#FFFFFF;
}
.active-tab {
    color: #ffffff;
}
button.active-tab:after {
    width: 100%;
}
button:hover:after {
    width: 100%;
}

