
.mobile-logo{
    width:40px;
    height:40px;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8' standalone='no'%3F%3E%3Csvg width='48' height='48' viewBox='0 0 48 48' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='24' cy='24' r='24' fill='white'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M24 0C24 0 24 24 0 24C23.5776 24.1714 24 48 24 48C24 48 24 24 48 24C24 24 24 0 24 0Z' fill='%230B0D17'/%3E%3C/svg%3E%0A");
    background-size: cover;
    background-repeat: no-repeat;
    top: 24px;
    left: 24px;
    position: fixed;
    z-index: 9999;
}
.mobile-header {
    width: 254px;
    height: 100%;
    position: fixed;
    top: 0;
    padding: 0 0;
    right: 0;
    left: auto;

    transition: 0.3s;
    transform: translateX(100%);
    padding-right: 0;
    padding-top: 0;
    z-index: 99999999;
}

.backdrop-blur{
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.03999999910593033);
    z-index: 9;
    -webkit-backdrop-filter: blur(50px);
    backdrop-filter: blur(50px);
    position: absolute;
}

.mobile-menu-items {
    position: absolute;
    z-index: 9999;
    padding-left: 32px;

    margin-top: 150px;
    font-family: Barlow Condensed;
}
.mobile-menu-item{
    margin-top: 50px;
}
.mobile-button-open{
    position: absolute;
    top: 33px;
    right: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 9999999999999999999999999;
}
.hamburger-react-div{
    z-index: 9999999999999999999999999;
    top: 24px;
    left: 85%;
    width: 48px;
    height: 48px;
    right: 0 !important;
    position: fixed;
}
    #mob-active {
        position: relative;
        color: #ffffff;
    }
     #mob-active:before {
        content: '';
    position: absolute;
    right: 0;
    left: 218px;
    top: 0;
    height: 100%;
    border-right: 4px solid #ffffff;
    color: #ffffff;
}
