@media only screen and (max-width: 600px) {
    .crew-container{
    width: 100%;
    height: 100vh;
    background-image: url('../imgs/Bitmap-crew-mob.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    }
    .crew-section-one{
    padding-top: 88px;
    }
    .crew-heading {
    width: 221px;
    text-align: center;
    right: 0;
    left: 0;
    margin: auto;
    }
    .crew-heading span{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    color: #FFFFFF;
    opacity: 0.25;
}
    .crew-heading p{
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    text-transform: uppercase;
    color: #FFFFFF;
    display: inline;
}
    .crew-img{
    width: 327px;
    height: 223px;
    right: 0;
    left: 0;
    margin: auto;
    display: flex;
    border-bottom: 1px solid #383B4B;
    }
.crew-img img {
    width: auto;
    height: 222px;
    margin: auto;
}
.crew-section-two{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 32px;
    margin-top: 32px;
}
.crew-controller{
    width: 88px;
    display: flex;
    gap: 16px;
}

.circle-btn{
    width: 10px;
    height: 10px;
    background-color: #979797;
    border-radius: 50px;
    border: none;
    transition: all .3s ease-in-out;
}
#crew-active{
    background: white;
}
.crew-info{
    width: 327px;
    height: 212px;
}
.crew-role{
font-family: 'Bellefair';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 18px;
text-align: center;
text-transform: uppercase;
color: #FFFFFF;
mix-blend-mode: normal;
opacity: 0.5;
}
.crew-name{
font-family: 'Bellefair';
font-style: normal;
font-weight: 400;
font-size: 24px;
line-height: 28px;
text-align: center;
text-transform: uppercase;
color: #FFFFFF;
}
.crew-bio{
font-family: 'Barlow';
font-style: normal;
font-weight: 400;
font-size: 15px;
line-height: 25px;
text-align: center;
color: #D0D6F9;

}

}
