  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 81px;
  /* justify-content: center; */
  align-content: center;
}
.home-section-one{
  padding-top: 112px;
  width: 327px;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  gap: 16px;

}
.home-section-one p{
margin-top: 0;
margin-bottom: 0;
}
.text-first{

font-family: 'Barlow Condensed';
font-style: normal;
