line-height: 25px;
text-align: center;
color: #D0D6F9;
}

.home-section-two{
  width: 150px;
  height: 150px;
  background: white;
  border-radius: 150px;
  position: relative;
  margin-top: 0;
}
.home-explore-upper{
  width: 150px;
  height: 150px;
  display: flex;
  align-items: center;
  z-index: 9999;
}
.desktop-home-explore{
font-family: 'Bellefair';
font-style: normal;
font-weight: 400;
font-size: 20px;
line-height: 23px;
text-align: center;
