  z-index: 9999 ;
}
.desktop-home-explore{
  font-family: 'Bellefair';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  letter-spacing: 2px;
  color: #0B0D17;
  width: 274;
  height: auto;
  margin: auto;
  transition: .3s ease-in-out;
}
.desktop-home-explore:hover{
  font-family: 'Bellefair';
font-style: normal;
font-weight: 400;
font-size: 32px;
