/* EXPLORE BUTTON */

.desktop-home-explore:after {
  content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(151, 151, 151, 0.35);
    aspect-ratio: 1;
    width: 100%;
    border-radius: 100%;
    z-index: -1;
    transition: 0.25s ease-out;
}
.desktop-home-explore:hover:after {
  width: 400px;
}

}












