.desktop-home-header{
    z-index: 99999;
    top: 40px;
    left: 55px;
    width: 1385px;
    height: 96px;
    display: flex;
    position: absolute;
    box-sizing: border-box;
    align-items: flex-start;
    flex-shrink: 1;
    border-color: transparent;
  }
  .header-logo{
    width: 48px;
    height: 48px;
    background-image:url('../imgs/88a590c5-d228-4664-b52f-c35f02a3bccb-l12s.svg') ;
    top: 24px;
    left: 0px;
    position: absolute;
    box-sizing: border-box;
    object-fit: cover;
    border-color: transparent;
  }

  /* Header Line */

  .header-line{
    width: 530px;
    height: 1px;
    background-image: url('../imgs/Rectangle.png');
    top: 48px;
    left: 112px;
    opacity: 0.25;
    position: absolute;
    box-sizing: border-box;
    object-fit: cover;
    border-color: rgba(150, 150, 150, 1);
    border-width: 0px;
    background-color: rgba(255, 255, 255, 1);
    z-index: 99;
  }
  /* HEADER MENU BOX */
  .header-menu{
    top: 0px;
      left: 400px;
      width: 970px;
      height: 96px;
      position: absolute;
      box-sizing: border-box;
      object-fit: cover;
      border-color: rgba(150, 150, 150, 1);
      border-width: 0px;
      background-color: rgba(255, 255, 255, 0.03999999910593033);

    }

  .header-main-menu {
    z-index: 55;
    top: 37px;
    left: 336px;
    width: 542px;
    height: 19px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    box-sizing: border-box;
    align-items: flex-start;
    flex-shrink: 1;
    border-color: transparent;
    color: white;
    font-family: Barlow Condensed;
    font-weight: 400px;
  }
  /* .header-main-menu div {

    height: 60px;
  } */
  .header-main-menu .desktop-home-menu-link:before ,
  .header-main-menu .desktop-home-menu-link:active:before  {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    left: 0;
    top: 55px;
    background-color:#979797;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out;
  }
  .header-main-menu .desktop-home-menu-link:hover:before ,
  .header-main-menu .desktop-home-menu-link:active:before {
    visibility: visible;
    transform: scaleX(1);
  }

  #active:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    left: 0;
    top: 55px;
    background-color: #FFF;
    visibility: visible;
    transform: scaleX(1);
    transition: all 0.3s ease-in-out;
 /* Active Under Line responsive (works on all devices expect mobile) */
  }

  .menu-50{

    position: relative;
  }

  .menu-100 {

    position: relative;
  }
  /* FILTER */
  .header-blur-filter{
      top: 0px;
      left: 0px;
      width: 970px;
      height: 96px;
      position: absolute;
      box-sizing: border-box;
      object-fit: cover;
      border-color: rgba(150, 150, 150, 1);
      border-width: 0px;
      background-color: rgba(255, 255, 255, 0.03999999910593033);
      z-index: 9;
      backdrop-filter: blur(10px);

  }

  .desktop-home-textNumber {
    margin-right: 7px;
    font-family: Barlow Condensed;
    font-weight: 400px;
    width: 70px;
  }

  @media only screen

  and (min-device-width: 768px)
  and (max-device-width: 1024px)
  and (-webkit-min-device-pixel-ratio: 1) {
    .header-line{
      display: none;
    }
    /* HEADER */

    .desktop-home-header{
      z-index: 99999;
      top: 0;
      left: 5%;
      width: 95%;
      height: 75px;
      position: absolute;
      align-items: flex-start;
      flex-shrink: 1;
      border-color: transparent;
      display: flex;
        align-items: center;
        justify-content: space-between;

    }
    .header-logo{
      position: relative;
      top: 5px;
    }
    /* HEADER MENU BOX */
    .header-menu{
      top: 0;
        left: 0;
        width: 450px;
        height: 96px;
        position: relative;
        box-sizing: border-box;
        object-fit: cover;
        border-color: rgba(150, 150, 150, 1);
        border-width: 0px;
        background-color: rgba(255, 255, 255, 0.03999999910593033);

      }
      .header-main-menu {
        z-index: 55;
        top: 0;
        left: 0;
        width: 450px;
        height: 100%;
        display: flex;
        position: relative;
        box-sizing: border-box;
        align-items: center;
        flex-shrink: 1;
        border-color: transparent;
        justify-content: center;
        align-items: center;
        padding: 0 0;
        gap: 30px;
      }

      .header-main-menu .desktop-home-menu-link:before ,
    .header-main-menu .desktop-home-menu-link:active:before  {
      content: "";
      position: absolute;
      width: 100%;
      height: 3px;
      bottom: 0;
      left: 0;
      top: 45px;
      background-color: #FFF;
      visibility: hidden;
      transform: scaleX(0);
      transition: all 0.3s ease-in-out;
    }
    .header-main-menu .desktop-home-menu-link:hover:before ,
    .header-main-menu .desktop-home-menu-link:active:before {
      visibility: visible;
      transform: scaleX(1);
    }
    .menu-50{

      position: relative;
    }

    .menu-100 {

      position: relative;
    }

      /* FILTER */

      .header-blur-filter{
       display: none;

      }
    /* REMOVE ALL NUMBERS IN MENU  */
    .desktop-home-text03 {
      display: none;
    }
    .desktop-home-text05 {
     display: none;
    }
    .desktop-home-text07 {
     display: none;
    }
    .desktop-home-text09 {
     display:  none;
    }
    .nav-menu-num{
      display: none;
    }

  }